
<template>
  <div class="features">
    <h2
      v-observe-visibility="{
        callback: () => {
          if (shownFeatures) {
            showFeatures = !showFeatures;
          } else {
            shownFeatures = !shownFeatures;
          }
        },
        once: true,
      }"
    >
      Features
    </h2>
    <transition-group
      name="slidein"
      tag="ul"
      v-bind:css="false"
      v-on:before-enter="beforeEnter"
      v-on:enter="enter"
    >
      <li
        v-for="(feature, index) in features"
        v-show="showFeatures"
        v-bind:key="feature.name"
        v-bind:data-index="index"
      >
        <h4>{{ feature.name }}</h4>
        <p>{{ feature.description }}</p>
      </li>
    </transition-group>
  </div>
</template>
<script>
import Velocity from "../../node_modules/velocity-animate/velocity";

export default {
  name: "features",
  data() {
    return {
      shownFeatures: false,
      showFeatures: false,
      features: [
        {
          name: "Workspaces",
          description:
            "Working on many project may lately become nightmare without proper requests organization - that's why workspaces are a great thing. Create requests and save them in each workspace - it's quiet nice.",
        },
        {
          name: "Documentation integration",
          description:
            "While you are managing a small or big project you may start writing a documentation for all diffrent things. And here you can connect requests manager to your documentation API - for now github or gitlab documentations API's are available.",
        },
        {
          name: "Default Darkmode ðŸŒ‘",
          description: "Yes, just as it should be.",
        },
      ],
    };
  },
  methods: {
    beforeEnter: function (el) {
      el.style.opacity = 0;
    },
    enter: function (el, done) {
      var delay = el.dataset.index * 450 + 550;
      setTimeout(function () {
        Velocity(
          el,
          { opacity: 1, translateY: "-20px"},
          { complete: done }
        );
      }, delay);
    },
  },
};
</script>

